import {GetSchoolInfomation} from '@/api/school'
export default {
	props:{
		schoolid:Number
	},
	data(){
		return{
			newsList:[],//新闻列表
			getParameters:{
				schoolid:this.schoolid,
				pageindex:1,
				pagesize:10
			},
			pageCount:0,
			refreshStatus:false,
			height:0,
		}
	},
	mounted(){
		uni.showLoading({
		    title: '加载中'
		});
		uni.getSystemInfo({
			success:res=>{
				this.height = res.windowHeight-226
			}
		})
		this.loadPage()
	},
	methods: {
		onRefresh(){
			this.loadPage()
		},
		open(item){
			var url = encodeURIComponent(item.weburl)
			uni.navigateTo({
				url:'/pages/web/web?title='+item.title+'&photo='+item.photo+'&url='+url
			})
		},
		scrolltolower(){
			this.getParameters.pageindex++
			if(this.getParameters.pageindex <= this.pageCount){
				this.loadPage('more')
			}
		},
		//初始数据的请求
		loadPage(status){
			if(this.loading){
				return
			}
			this.loading = true
			if(status=='more'){
				GetSchoolInfomation(this.getParameters).then(res=>{
					if(res.result){
						this.newsList = this.askList.concat(res.data)
					}
					this.loading = false
				})
			}else{
				this.getParameters.pageindex=1
				GetSchoolInfomation(this.getParameters).then(res=>{
					if(res.result){
						this.pageCount = res.pageCount
						this.newsList = res.data
					}else{
						this.newsList = []
						this.pageCount = 0
					}
					uni.hideLoading();
					this.loading = false
					setTimeout(()=>{
						this.refreshStatus = false
					},1000)
				})
			}
		}
		
	}
}
